import { useEffect, useState } from "react";
import PropTypes from "prop-types";

import Form, { useForm } from "@miq/form";
import { AdminView } from "@miq/admin";

import { departmentServices } from "./utils";

const propTypes = {};

export default function DepartmentUpdateView(props) {
    const [data, setData] = useState({ user: {} });
    const { compSlug } = props.match.params;
    const { depSlug } = props.match.params;

    useEffect(() => {
        if (!depSlug || !compSlug) return;

        departmentServices
            .get(depSlug, { company: compSlug })
            .then((empData) => {
                setData(empData);
            })
            .catch((err) => {});
    }, [depSlug, compSlug]);

    console.log(data);

    return <AdminView className="department-update-view" title={<>{`< ${data.name}`}</>}></AdminView>;
}

DepartmentUpdateView.propTypes = propTypes;
